export default {
  props: {
    ArticleCover: {
      type: String,
      default: ""
    },
    ArticlePublish: {
      type: String,
      default: "2101-11-22"
    },
    ArticleTitle: {
      type: String,
      default: "This is the Title"
    },
    ArticleSlug: {
      type: String
    },
    ArticleView: {
      type: Number
    },
    article: {
      type: String
    },
    imgAlt: {
      type: String
    }
  },
  data() {
    return {
      icon: "star",
      toogle: false,
      active2: false,
      iconColor: "dark"
    };
  },
  methods: {
    checkLocal() {
      for (let i = 0; i < localStorage.length; i++) {
        let key = localStorage.key(i);
        let value = localStorage.getItem(key);
        if (key == this.ArticleSlug) {
          // console.log("found");
          this.toogle = true;
          this.icon = "star-fill";
          this.iconColor = "warning";
          break;
        }
      }
    },
    setFavourite(duration, color) {
      if (process.browser) {
        this.toogle = !this.toogle;
        if (this.toogle) {
          localStorage.setItem(
            this.ArticleSlug,
            JSON.stringify(this.ArticleTitle)
          );
          this.icon = "star-fill";
          this.iconColor = "warning";

          const noti = this.$vs.notification({
            duration,
            color,

            progress: "auto",
            title: "Added",
            text:
              "This article Successfully added to Favourite.Check Favourite Section"
          });
        } else if (!this.toogle) {
          for (let i = 0; i < localStorage.length; i++) {
            let key = localStorage.key(i);
            let value = localStorage.getItem(key);
            if (key == this.ArticleSlug) {
              // console.log("found");
              localStorage.removeItem(this.ArticleSlug);
              break;
            }
          }
          const noti = this.$vs.notification({
            duration: 6000,
            color: "#dc3545",

            progress: "auto",
            title: "Removed",
            text:
              "This article Successfully Removed from Favourite.Click again to added!"
          });

          this.icon = "star";
          this.iconColor = "dark";
        }
      }
    }
  },
  mounted() {
    this.checkLocal();
  }
};
