import VueContentLoading from "vue-content-loading";

export default {
  components: {
    VueContentLoading
  },
  name: "Carousel",
  data() {
    return {
      slide: 0,
      sliding: null,
      data: [],
      loadingImg: true
    };
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    }
  },
  async mounted() {
    // await this.$axios
