export default {
  props: {
    pathUrl: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      modalActive: this.activeModal,
      place: `http://test.resultonlinebd.com${this.pathUrl}`
      //   active: this.activeModal
    };
  },
  methods: {
    copyLink() {
      if (process.browser) {
        var copyText = document.getElementById("myInput");
        copyText.select();
        copyText.setSelectionRange(0, 99999);
        document.execCommand("copy");

        // navigator.clipboard.writeText(this.place);
        const noti = this.$vs.notification({
          duration: 4000,
          color: "#4a5153",

          progress: "auto",
          title: "Copied",
          text: "This article link is  copied."
        });
      }
    },
    shareToFb() {
      window.open(
        "https://www.facebook.com/dialog/share?app_id=2141341249515400&display=popup&href=http://test.resultonlinebd.com" +
          this.pathUrl,
        "_blank"
      );
    },
    shareToTw() {
      window.open(
        "https://www.facebook.com/dialog/share?app_id=2141341249515400&display=popup&href=http://test.resultonlinebd.com" +
          this.pathUrl,
        "_blank"
      );
    },
    shareToIn() {
      window.open(
        "https://www.facebook.com/dialog/share?app_id=2141341249515400&display=popup&href=http://test.resultonlinebd.com" +
          this.pathUrl,
        "_blank"
      );
    }
  },
  mounted() {},
  computed: {
    // modalActive() {
    //   return this.activeModal;
    // }
  }
};
