export default {
  name: "NavBar",
  data() {
    return {
      keyword: "",
      show: true,
      countries: [],
      suggested: "",
      showRightMenuCard: false,
      toogle: false
    };
  },
  methods: {
    showRightMenu() {
      this.toogle = !this.toogle;
      if (this.toogle) {
        // console.log("show");
        this.showRightMenuCard = true;
      } else if (!this.toogle) {
        // console.log("hide");
        this.showRightMenuCard = false;
      }
    },
    commingSoon() {
      const noti = this.$vs.notification({
        duration: 4000,
        color: "#4a5153",

        progress: "auto",
        title: "Comming Soon",
        text: "This section is now under construct.We will launch it very soon!"
      });
    },
    searchinfo() {
      if (this.keyword != "") {
        this.$router.push("/search/" + this.keyword);
      }
    },
    customMethod() {
      this.show = true;
      // this.showSearch = true;
    },
    searchData(input) {
      return new Promise(resolve => {
        if (input.length < 2) {
          return resolve([]);
        }

        fetch(process.env.baseUrl + "/serach/" + input)
          .then(response => response.json())
          .then(data => {
            resolve(data.results);
          })
          .catch(e => console.log());
        this.keyword = input;
      });
    },

    getResultValue(result) {
      return result.title;
    },
    handleSubmit(result) {
      event.preventDefault();
      try {
        this.$router.push("/search/" + result.title);
      } catch (error) {
        console.log(this.keyword);
        this.$router.push("/search/" + this.keyword);
      }
    }
  },
  computed: {
    // showSearch() {
    //   if (this.show == false) {
    //     return true;
    //   } else {
    //     return false;
    //   }
    // }
  },
  mounted() {}
};
